html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  font-size: 16px;
  line-height: 1.5;
  color: #f5f5f5;
  @extend %s400;
}

.wrapper {
  margin: 0 auto;
  max-width: 1920px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  font-size: 48px;
  white-space: nowrap;
  text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.2),
    1px 1px 1px rgba(0, 0, 0, 0.6);
  color: $title;
  transition: transform 0.6s;
  @include mq("768") {
    top: 30%;
    font-size: 2rem;
  }
  @extend %l500;
}

.hover-right {
  & .split--right {
    width: 75%;
    @include mq("1100") {
      width: 50%;
    }
    @include mq("600") {
      width: 90%;
    }
  }

  & .split--left {
    width: 25%;
    @include mq("1100") {
      width: 50%;
    }
    @include mq("600") {
      overflow: hidden;
      width: 10%;

      .title.content__title {
        margin-top: 160px;
        transform: rotate(-90deg);
      }

      .cards {
        transform: translateX(-100vh);
      }
    }
  }
}

.hover-left {
  & .split--right {
    width: 25%;
    @include mq("1100") {
      width: 50%;
    }
    @include mq("600") {
      overflow: hidden;
      width: 10%;

      .title.contacts__title {
        margin-top: 160px;
        transform: rotate(-90deg);
      }

      .contacts__inner {
        transform: translateX(100vh);
      }
    }
  }

  & .split--left {
    width: 75%;
    @include mq("1100") {
      width: 50%;
    }
    @include mq("600") {
      width: 90%;
    }
  }

  & .contacts__inner {
    flex-wrap: wrap;
    justify-content: center;
  }

  & .contacts__body {
    margin: 0 0 30px;
    min-width: 100%;
  }

  & .contacts__content {
    animation-name: "op";
    animation-duration: 0.8s;
    animation-timing-function: ease;
  }
}

.portfolio {
  display: flex;
  min-width: 100%;
  min-height: 100vh;
  background-color: #fff;
}

.split {
  overflow: hidden;
  width: 50%;
  min-height: 800px;
  max-height: 100vh;
  @include mq("1100") {
    overflow-x: hidden;
    overflow-y: scroll;
  }

  &.split--left {
    // background-color: $bg2;
    background-image: linear-gradient(
      to bottom,
      #e4f9f5,
      #d5f6f2,
      #c5f2f1,
      #b5eff1,
      #a4ebf3
    );
  }

  &.split--right {
    // background-color: $bg1;
    background-image: linear-gradient(
      to right bottom,
      #30e3ca,
      #00c3be,
      #00a3ac,
      #1a8494,
      #276678
    );
  }

  &.split--left,
  &.split--right {
    transition: width 0.6s ease-in-out;
  }
}
