.contacts {
  &__inner {
    display: flex;
    justify-content: center;
    margin: 0 15px;
    transition: transform 0.6s;
    @include mq("1400") {
      flex-wrap: wrap;
    }
  }

  &__body {
    margin-right: 40px;
    @include mq("1400") {
      margin-right: 0;
    }
  }

  &__content {
    margin: 0 20px 30px;
    border-radius: 6px;
    padding: 10px;
    min-width: 280px;
    max-width: 65%;
    font-size: 15px;
    color: #000;
    background-color: rgba(0, 0, 0, 0.1);
    user-select: none;
  }

  &__top-title {
    margin-bottom: 20px;
    font-size: 18px;
  }

  &__middle-title {
    margin-bottom: 10px;
  }

  &__middle-list {
    list-style: inside;
    margin-bottom: 15px;

    li {
      margin-bottom: 6px;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    @include mq("1100") {
      margin-bottom: 30px;
    }
  }

  &__item {
    transition: transform 0.4s;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 15px;
    }

    &:hover {
      transform: scale(1.08);
    }
  }

  &__link {
    font-size: 1rem;
    color: #ececec;
  }

  &__icon {
    max-width: 50px;
    max-height: 50px;
  }
}
